import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { ApiService } from 'src/app/services/api.service';

@Component({
  selector: 'app-currencyconverter',
  templateUrl: './currencyconverter.page.html',
  styleUrls: ['./currencyconverter.page.scss'],
})
export class CurrencyconverterPage implements OnInit {
  currencyForm: FormGroup;
  accountCurrencyFlag = 'in';
  isShow: boolean = true;
  accountCurrencyFlag1 = 'us';
  isShow1: boolean = true;
  currjson: any=[];
  exchangeRate: any;
  
 



  constructor(private fb: FormBuilder, private router: Router,private api: ApiService) { }

  ngOnInit() {  
    this.currencyForm = this.fb.group({
      amount: [''],
      fromCurrency: [ localStorage.getItem('Account_Currency')],
      toCurrency: [ localStorage.getItem('Transaction_Currency')],
      exchangeRate: [''],
    })
   // this.currency()
  }
  amount(event){
   
    this.currency();

  }
currency(){
  this.api.getExchangeRate(this.currencyForm.value.fromCurrency).subscribe((item:any)=>{
    this.currjson = item;
   var toCurrency=this.currencyForm.value.toCurrency
  this.exchangeRate=this.currjson.rates[toCurrency];
  var finalExchange=this.exchangeRate * this.currencyForm.value.amount
  this.currencyForm.get('exchangeRate').patchValue(finalExchange);

    })
}

  previous2(){
    this.router.navigate(['forex-transaction'])
  }

}
